import React from 'react'
import './style.css'

class About extends React.Component {
  render () {
    return(
      <div className="about">
        <div className="avatar">
          <img src={require('../../components/assets/img/me2.png')}/>
        </div>
        <div className="about-text">
          <h1>Обо мне</h1>
          <p>
          Меня зовут Данабек.
          Год назад переехал в 
          Алматы с целью стать 
          программистом Мой первый проект был чат-бот на python 
          Закончил несколько курсов по веб-разработке. Позже выдвинул
          проект c командой на hackaton 2018 В свободное время выполняю 
          заказы на freelance. В данное время изучаю React js
          </p>
        </div>
      </div>
    )
  }
}

export default About;
