import React,{Component} from 'react'
import './style.css'

import PROJECTS from '../../components/assets/data/WorkData.js'


class ProjectDetail extends Component {
  
  
  render(){
    console.log(PROJECTS[this.props.match.params.projectId]);
    return(
      <>
        <div className="Project-title">
          <h1>{PROJECTS[this.props.match.params.projectId].name}</h1>
          <p>{PROJECTS[this.props.match.params.projectId].description}</p>
          <div>
            <a>Посмотреть код</a> <a>Открыть проект</a>
          </div>
        </div>
        <div className="description">
          <div className="tech">
            <h2>Стэк технологии</h2>
            {PROJECTS[this.props.match.params.projectId].tech}
          </div>
          <div className="team">
            <h2>Команда</h2>
            {PROJECTS[this.props.match.params.projectId].team}
          </div>
        </div>
      </>
  
      
    )
  }
}


export default ProjectDetail
